<template>
    <div>
        <navbar title="邀请好友" v-if="$phonemodel == 'Android'"></navbar>
        <div class="app" :class="{nav:$phonemodel == 'Android'}">
            <div class="firstPage"><p class="logo"><img src="@/static/images/my/yaoqing_bi@2x.png" alt=""></p></div>
            <div class="twoPage">
                <div class="text">
                    <div class="main">
                        <p class="title">我的邀请码</p>
                        <p class="code" id="invCode">{{inviteData.invite_code}}</p>
                        <button class="copy" @click="copy(inviteData.invite_code)">复制</button>
                        <hr />
                        <img :src="inviteData.invite_qrcode" alt />
                        <p class="bottom">扫描二维码，拯救负债者！</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {invite} from "../../api/center";
    import { toast, Dialog } from 'vant';
    import navbar from "@/components/navbar";
    import Clipboard from 'clipboard'
    export default {
        name: "Transfer",
        components: {
            navbar
        },
        data()
        {
            return {
                inviteData:{}
            }
        },
        mounted()
        {
            this.getInvite()
        },
        methods:{
            getInvite()
            {
                invite().then(res => {
                    this.inviteData = res

                })
            },
            // 复制邀请码
            copy(copyText)
            {

                var clipboard = new Clipboard('.copy', {
                    text: function() {
                        return copyText;
                    }
                });
                clipboard.on('success', e => {

                    toast('复制成功')
                    // 释放内存
                    clipboard.destroy()
                })
                clipboard.on('error', e => {
                    toast('复制失败')
                    // 释放内存
                    clipboard.destroy()
                })

            }
        }
    }
</script>

<style scoped>
    @import "../../static/css/invite.css";
</style>



